.page {
  height         : 100%;
  scroll-behavior: smooth;
}

.page__body {
  display           : grid;
  grid-template-rows: min-content 1fr min-content;
  align-content     : start;
  min-height        : 100%;
  margin            : 0;
  background-color  : $white;
  font-family       : "PT Mono", "Arial", sans-serif;
  font-weight       : 400;
  font-size         : 22px;
  line-height       : 22px;
  color             : $black;
}
